form {
  counter-reset: form-section;
}

  form .form-wrapper {
    counter-reset: form-fields;
    padding: 20px 20px 20px 0;
  }

  form .form-wrapper .form-title {
      color: #484848;
      position: relative;
      padding: 0;
      cursor: default;
      padding-left: 10px;
      line-height: 140%;
      text-align: left;
      font-size: 24px;
    }

  form .form-wrapper .form-title:before {
        counter-increment: form-section;
        content: counter(form-section) ". ";
        color: #008489;
      }

  form .form-wrapper .form-subtitle {
      margin-bottom: 40px;
      font-size: 16px;
      -webkit-font-smoothing: antialiased;
      line-height: 160%;
      text-align: left;
      color: #333333;
      -khtml-opacity: .8;
      -moz-opacity: .8;
      opacity: .8;
    }

  form .form-wrapper .form-content {
      padding-left: 30px;
    }

  form .form-wrapper .form-group .control-label {
        text-align: left;
        font-size: 18px;
      }

  form .form-wrapper .form-group .control-label:before {
          counter-increment: form-fields;
          content: counter(form-fields, lower-alpha) ". ";
          color: #008489;
        }

.input-group-addon {
  border-width: 1px;
  border-bottom-color: lightgrey;
  border-style: solid;
}

.errored {
  border-width: 0px !important;
  border-bottom-width: 2px !important;
  border-bottom-color: #FF5A5F !important;
  border-style: solid !important;
}

.focused {
  border-width: 0px !important;
  border-bottom-width: 2px !important;
  border-bottom-color: #008489 !important;
  border-style: solid !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}

.checkbox-content {
  display: content;
}

.checkbox input[type="checkbox"] {
      position: relative;
      -webkit-appearance: none;
      height: 1.25em !important;
      width: 1.25em !important;
      margin: 5px 10px 0 0 !important;
      vertical-align: top;
      border: 1px solid #aaa !important;
      border-radius: 0px !important;
      background-color: #FFFFFF !important;
      color: #484848 !important;
  }

.checkbox input[type="checkbox"]:checked:before {
          content: "\2713";
          position: absolute;
          font-size: 0.85em;
          left: 1px;
          text-align: center;
          width: 1.25em;
          color: #008489;
        }



